 .tile {
     background: beige;
     display: inline-block;
 }
 
 .rock {
     background-image: url('./rock.png')
 }

 .chest {
     background-image: url('./chest.png')
 }

 .tree {
     background-image: url('./tree.png')
 }

 .npc {
     background-image: url('./player.png')
 }

 .water {
     background-image: url('./WATER_IMG.gif')
 }

 .light {
     background: -webkit-gradient(linear, left top, left bottom, from(yellow), to(rgb(58, 58, 51)));
     background: -webkit-linear-gradient(yellow, rgb(58, 58, 51));
     background: -o-linear-gradient(yellow, rgb(58, 58, 51));
     background: linear-gradient(yellow, rgb(58, 58, 51));
 }




